.formulario{
    display: flex;
    justify-content: space-between;
    margin: 80px 50px;
    flex-wrap:wrap ;
    gap: 20px;
}

.formulario form{
    flex: 1;
    background-color: #F7F7F7;
    border-radius: 20px;
    box-shadow: 7px 7px 15px rgba(0, 0, 0, 0.25);
    padding: 8px 100px;
}

.formulario h2{
    font-size: 32px;
    font-weight: 400;
}

